<template>
  <div class="container">
    <el-form
      :model="findPsInfo"
      :rules="findPsRules"
      ref="ruleForm"
      label-width="100px"
      status-icon
      class="demo-ruleForm"
      label-position="left"
    >
      <el-form-item label="学号" prop="stuId">
        <el-input v-model="findPsInfo.stuId" placeholder="请输入学号"></el-input>
      </el-form-item>
      <el-form-item label="安全邮箱" prop="email">
        <el-input v-model="findPsInfo.email" placeholder="请输入邮箱"></el-input>
      </el-form-item>
      <el-form-item label="新密码" prop="newPassWord">
        <el-input v-model="findPsInfo.newPassWord" placeholder="请输入密码"></el-input>
      </el-form-item>
      <el-form-item label="确认密码" prop="reNewPassWord">
        <el-input v-model="findPsInfo.reNewPassWord" placeholder="再次输入密码"></el-input>
      </el-form-item>
      <el-row>
        <el-col :span="16">
          <el-form-item label="邮箱验证码" prop="emailCode">
            <el-input v-model.number="findPsInfo.emailCode" placeholder="验证码"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="5" :offset="3">
          <el-button style="width: 100%" v-show="show" @click="handleEamilCode()">获取</el-button>
          <el-button v-show="!show">{{ count }} S</el-button>
        </el-col>
      </el-row>
      <el-button style="width: 100%" type="primary" @click="submitFindPs()">确 定</el-button>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "FindPs",
  data() {
    var validatePass2 = (rule, reNewPassWord, callback) => {
      // 校验确认密码
      if (reNewPassWord === "") {
        callback(new Error("请再次输入密码"));
      } else if (reNewPassWord !== this.findPsInfo.newPassWord) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    var checkEmail = (rule, email, callback) => {
      // 校验邮箱
      const mailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/;
      if (!email) {
        return callback(new Error("邮箱不能为空"));
      }
      setTimeout(() => {
        if (mailReg.test(email)) {
          callback();
        } else {
          callback(new Error("请输入正确的邮箱格式"));
        }
      }, 100);
    };
    var validateemailCode = (rule, emailCode, callback) => {
      // 校验验证码
      if (!emailCode) {
        callback(new Error("请输入验证码"));
      } else if (emailCode.toString().length !== 6) {
        callback(new Error("请输入4位验证码!"));
      } else {
        callback();
      }
    };
    return {
      show: true, // 初始启用按钮
      count: "", // 初始化次数
      timer: null,
      findPsInfo: {
        stuId: "",
        email: "",
        newPassWord: "",
        reNewPassWord: "",
        emailCode: null,
        isReg: 1
      },
      findPsRules: {
        stuId: [{ required: true, message: "请输入学号", trigger: "blur" }],
        email: [
          { required: true, message: "请输入邮箱", trigger: "blur" },
          { validator: checkEmail, trigger: "blur" }
        ],
        newPassWord: [
          { required: true, message: "请输入新密码", trigger: "blur" }
        ],
        reNewPassWord: [
          { required: true, message: "请再次输入新密码", trigger: "blur" },
          { validator: validatePass2, trigger: "blur" }
        ],
        emailCode: [
          { validator: validateemailCode, trigger: "blur" },
          { type: "number", message: "验证码必须是数字" },
          { required: true, message: "请输入邮箱验证码", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    handleEamilCode() {
      console.log("handleEamilCode");
      this.$message.success("验证码正在发送，请稍等");
      const TIME_COUNT = 60; //更改倒计时时间
      if (!this.timer) {
        this.count = TIME_COUNT;
        this.show = false;
        this.timer = setInterval(() => {
          if (this.count > 0 && this.count <= TIME_COUNT) {
            this.count--;
          } else {
            this.show = true;
            clearInterval(this.timer); // 清除定时器
            this.timer = null;
          }
        }, 1000);
      }
      this.$axios({
        url: "/api/user/validationemail",
        method: "post",
        data: {
          email: this.findPsInfo.email
        }
      })
        .then(res => {
          console.log(res);
          if (res.data.msg === "邮件已发送") {
            this.$message.success("验证码发送成功");
          }
        })
        .catch(err => {
          alert("网络异常，请重试");
        });
    },
    submitFindPs() {
      console.log("submitFindPs");
      this.$message.success("正在修改，请稍等");
      this.$axios({
        url: "/api/user/findpassword",
        method: "post",
        data: {
          stuId: this.findPsInfo.stuId,
          email: this.findPsInfo.email,
          verificationCode: this.findPsInfo.emailCode,
          password: this.findPsInfo.newPassWord
        }
      })
        .then(res => {
          console.log(res);
          if (res.data.msg === "密码更新成功") {
            this.$message.success("密码更新成功");
          } else {
            this.$message.err(res.data.msg);
          }
        })
        .catch(err => {
          alert("网络异常，请重试");
        });
    }
  }
};
</script>