<template>
  <div class="container">
    <el-form
      :model="regInfo"
      :rules="regRules"
      ref="ruleForm"
      label-width="100px"
      status-icon
      class="demo-ruleForm"
      label-position="left"
    >
      <el-form-item label="学号" prop="stuId">
        <el-input v-model="regInfo.stuId" placeholder="请输入学号"></el-input>
      </el-form-item>
      <el-form-item label="姓名" prop="userName">
        <el-input v-model="regInfo.userName" placeholder="请输入姓名"></el-input>
      </el-form-item>
      <el-form-item label="安全邮箱" prop="email">
        <el-input v-model="regInfo.email" placeholder="请输入邮箱"></el-input>
      </el-form-item>
      <el-form-item label="新密码" prop="passWord">
        <el-input v-model="regInfo.passWord" placeholder="密码"></el-input>
      </el-form-item>
      <el-form-item label="确认密码" prop="rePassWord">
        <el-input v-model="regInfo.rePassWord" placeholder="再次输入密码"></el-input>
      </el-form-item>
      <el-row>
        <el-col :span="16">
          <el-form-item label="邮箱验证码" prop="emailCode">
            <el-input v-model.number="regInfo.emailCode" placeholder="验证码"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="5" :offset="3">
          <el-button style="width: 100%" v-show="show" @click="handleEamilCode()">获取</el-button>
          <el-button v-show="!show">{{ count }} S</el-button>
        </el-col>
      </el-row>
      <el-form-item label="性别">
        <el-radio v-model="regInfo.sex" label="0">女</el-radio>
        <el-radio v-model="regInfo.sex" label="1">男</el-radio>
      </el-form-item>
      <el-form-item></el-form-item>
    </el-form>
    <el-button style="width: 100%" type="primary" @click="submitReg()">确 定</el-button>
  </div>
</template>

<script>
import bus from "../js/bus";
export default {
  name: "Register",
  data() {
    var validatePass2 = (rule, rePassWord, callback) => {
      // 校验确认密码
      if (rePassWord === "") {
        callback(new Error("请再次输入密码"));
      } else if (rePassWord !== this.regInfo.passWord) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    var checkEmail = (rule, email, callback) => {
      // 校验邮箱
      const mailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/;
      if (!email) {
        return callback(new Error("邮箱不能为空"));
      }
      setTimeout(() => {
        if (mailReg.test(email)) {
          callback();
        } else {
          callback(new Error("请输入正确的邮箱格式"));
        }
      }, 100);
    };
    var validateemailCode = (rule, emailCode, callback) => {
      // 校验验证码
      if (!emailCode) {
        callback(new Error("请输入验证码"));
      } else if (emailCode.toString().length !== 6) {
        callback(new Error("请输入4位验证码!"));
      } else {
        callback();
      }
    };
    return {
        show: true, // 初始启用按钮
      count: "", // 初始化次数
      timer: null,
      regInfo: {
        stuId: "",
        userName: "",
        email: "",
        passWord: "",
        rePassWord: "",
        emailCode: null,
        sex: "0"
        // isReg: 1
      },
      regRules: {
        stuId: [{ required: true, message: "请输入学号", trigger: "blur" }],
        userName: [
          { required: true, message: "请输入用户名", trigger: "blur" }
        ],
        email: [
          { validator: checkEmail, trigger: "blur" },
          { required: true, message: "请输入邮箱", trigger: "blur" }
        ],
        passWord: [
          { required: true, message: "请输入新密码", trigger: "blur" }
        ],
        rePassWord: [
          { validator: validatePass2, trigger: "blur" },
          { required: true, message: "请再次输入密码", trigger: "blur" }
        ],
        emailCode: [
          { validator: validateemailCode, trigger: "blur" },
          { type: "number", message: "验证码必须是数字" },
          { required: true, message: "请输入验证码", trigger: "blur" }
        ]
      }
    };
  },
  computed: {
    comRegInfo() {
      let data = this.regInfo;
      data.sex = parseInt(data.sex);
      return data;
    }
  },
  methods: {
    handleEamilCode() {
      console.log("handleEamilCode");
      this.$message.success("验证码正在发送，请稍等");
      const TIME_COUNT = 60; //更改倒计时时间
      if (!this.timer) {
        this.count = TIME_COUNT;
        this.show = false;
        this.timer = setInterval(() => {
          if (this.count > 0 && this.count <= TIME_COUNT) {
            this.count--;
          } else {
            this.show = true;
            clearInterval(this.timer); // 清除定时器
            this.timer = null;
          }
        }, 1000);
      }
      this.$axios({
        url: "/api/user/validationemail",
        method: "post",
        data: {
          email: this.comRegInfo.email
        }
      })
        .then(res => {
          console.log(res);
          if (res.data.msg === "邮件已发送") {
            this.$message.success("验证码发送成功");
          }
        })
        .catch(err => {
          alert("网络异常，请重试");
        });
    },
    submitReg() {
      //提交注册表单
      console.log("submitReg");
      this.$message.success("正在注册，请稍等");
      this.$axios({
        url: "/api/user/register",
        method: "post",
        data: {
          stuId: this.comRegInfo.stuId,
          studentName: this.comRegInfo.userName,
          email: this.comRegInfo.email,
          verificationCode: this.comRegInfo.emailCode,
          password: this.comRegInfo.passWord,
          sex: this.comRegInfo.sex,
          state: 1
        }
      })
        .then(res => {
          console.log(res);
          if (res.data.msg === "success") {
            this.$message.success("注册成功");
          } else {
            this.$message.warning(res.data.msg);
          }
        })
        .catch(err => {
          alert("网络异常，请重试");
        });
    }
  }
};
</script>

